<template>
  <div class="">
    <HeadTop :Headtype="Headtype" :title="$t('lock.锁池')"></HeadTop>
    <div class="p15">
      <div class="lock-block">
        <div class="lock-tit">{{$t('lock.认证流动性储物柜')}}</div>
        <div class="lock-img"><img src="@/assets/images/lock.png"></div>
        <div class="lock-down border-1px-b">
          <van-count-down millisecond :time="time">
            <template #default="timeData">
              <span class="block">{{ timeData.days }}</span>
              <span class="colon">:</span>
              <span class="block">{{ timeData.hours }}</span>
              <span class="colon">:</span>
              <span class="block">{{ timeData.minutes }}</span>
              <span class="colon">:</span>
              <span class="block">{{ timeData.seconds }}</span>
            </template>
          </van-count-down>
        </div>
        <div class="lock-info__item">
          <div class="lock-item flex-between">
            <div class="item-tit">{{$t('lock.LP令牌总数')}}</div>
            <div class="item-info">{{lpAmount}}</div>
          </div>
          <div class="lock-item flex-between">
            <div class="item-tit">{{$t('lock.锁定的LP')}}</div>
            <div class="item-info number">{{lpAmount}}</div>
          </div>
          <div class="lock-item flex-between">
            <div class="item-tit">{{$t('lock.解锁日期')}}</div>
            <div class="item-info">2026-07-21 08:00:00</div>
          </div>
          <div class="lock-item flex-between">
            <div class="item-tit">{{$t('lock.开源')}}</div>
            <div class="item-info">2021-07-21 08:00:00</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import ArtAbi from "@/utils/ArtAbi";
import tokenAbi from "@/utils/abi";
import swapAbi from '@/utils/swapAbi';
import factoryAbi from '@/utils/factoryAbi';
import pairAbi from '@/utils/pairAbi';
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      Headtype: '',
      time: null,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      artContract: null,
      artTokenContract: null,
      routerContract: null,
      lpAmount: '0.000',
      Unlocktime: null,
      newTime: null,
      initContract: () => {
        data.artContract = new data.web3.eth.Contract(ArtAbi, config.ArtAddress)
        data.artTokenContract = new data.web3.eth.Contract(tokenAbi, config.TokenAddress)
        data.routerContract = new data.web3.eth.Contract(swapAbi, config.SwapRouter)
        data.initData()
      },
      
      initData: () => {
        var date = new Date('2026-07-19 08:00:00');
        data.Unlocktime = date.getTime();
        data.newTime = parseInt(Date.now())
        console.log(data.Unlocktime, data.newTime);
        data.time = data.Unlocktime - data.newTime
        data.getReverse()
        // data.getData()
      },
      getReverse: async () => {
        let WETH = await data.routerContract.methods.WETH().call()
        let factory = new data.web3.eth.Contract(factoryAbi, config.FactoryAddress)
        let pair = await factory.methods.getPair(WETH, config.TokenAddress).call()
        let pairContract = new data.web3.eth.Contract(pairAbi, pair)
        let amount = await pairContract.methods.totalSupply().call()
        data.lpAmount = Number(amount / Math.pow(10, 18)).toFixed(4);
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
  .lock-block{
    background: url(~@/assets/images/lock-bg.png) no-repeat;
    background-size: 100% 100%;
    padding: 200px 0 50px;
    min-height: 509px;
    text-align: center;
    .lock-tit{
      font-size: 19px;
      color: #333333;
      line-height: 1;
    }
    .lock-img{
      width: 25px;
      height: 32px;
      margin: 16px auto;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .lock-down{
      padding-bottom: 30px;
      .colon {
        display: inline-block;
        margin: 0 4px;
        color: #0066EE;
        font-size: 20px;
      }
      .block {
        display: inline-block;
        min-width: 22px;
        color: #fff;
        height: 31px;
        line-height: 31px;
        font-size: 15px;
        text-align: center;
        background-color: #0066EE;
      }
    }
    .lock-info__item{
      margin-top: 12px;
      padding: 0 15px;
      font-size: 13px;
      .lock-item{
        height: 28px;
        line-height: 28px;
        .item-tit{
          color: #666666;
        }
        .item-info{
          color: #333333;
          font-weight: bold;
        }
        .number{
          color: #0066EE;
        }
      }
    }
  }
</style>
